body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: stretch;
}

.block {
  display: flex;
  min-width: 300px;
  height: 300px;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  flex-grow: 1;
}

.block-1 {
  background-color: red;
  order: -2;
}

.block-2 {
  background-color: rgb(80% 0% 0%);
  order: -1;
}

.block-3 {
  background-color: rgb(60% 0% 0%);
  order: 0;
}

.block-4 {
  background-color: rgb(40% 0% 0%);
  order: 1;
}

.block-5 {
  background-color: rgb(20% 0% 0%);
  order: 2;
}

.block-6 {
  background-color: rgb(0% 0% 0%);
  order: 3;
}

@media (min-width: 600px) {
  .container {
    flex-direction: row;
  }

  .block-3 {
    order: 1;
  }

  .block-4 {
    order: 0;
  }

  .block-5 {
    order: 2;
  }

  .block-6 {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block {
    flex-basis: 33.33%;
  }

  .block-4 {
    order: 3;
  }
  
  .block-5 {
    order: 2;
  }
  
  .block-6 {
    order: 1;
  }
}
